.ski {
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8) url('../../Assets/code.jpg') center
      center/cover no-repeat;
    filter: blur(3px);
  }
  .skillSet {
    position: absolute;
    top: 120px;
    /* left: 110px; */
    align-self: center;
  }
  .row h2 {
    color: rgb(1, 26, 29);
    text-shadow: 1px 1px 1px #000;
    font-family: Georgia, 'Times New Roman', Times, serif;
  }
  
  .col {
    color: rgb(5, 63, 57);
    text-align: center;
  }
  
  .icon {
    width: 40px;
    height: 40px;
    margin-bottom: 5px;
  }
  .col p {
    font-family: 'Courgette', cursive;
    font-size: 1rem;
    text-shadow: 1px 0.5px 1px #000;
    color: rgb(237, 243, 248);
  }
  .icon:hover {
    border-bottom: rgb(247, 6, 6) solid 2px;
    font-size: 30px ease;
  }